.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-search-block {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  width: 95%;
}

.App-input-search {
  margin-top: 1rem;
  padding-left: 1rem;
  height: 2rem;
  width: 25rem;
}

.App-total-count {
  margin-top: 1rem;
}

.App-appstate-loading {
  margin-top: 2rem;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
